*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}
body{
    background-color: #555;
}
.calander{
    width: 400px;
    margin: 50px auto;
    background-color: #fff;
    padding: 10px;
}
.header{
    display: flex;
    justify-content: space-between;
    background-color: rgb(25, 111, 186);
    padding: 5px;
}
button{
    background-color: #fff;
    padding: 0 5px;
    font-size: 16px;
    border: none;
}
select{
    background-color: #fff;
    padding: 0px 10px;
    border: 1px solid blue;
    border-radius: 4px;
    margin: 0 10px;
}
.daysOfWeek{
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    background-color: rgb(25, 111, 186);
    padding: 5px;
    text-align: center;
    align-items: center;
    font-weight: bold;
    margin-bottom: 10px;
}
.days{
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    padding: 10px;
    text-align: center;
    align-items: center;
    gap: 8px;
}
.day,.empty{
    padding: 10px;
    text-align: center;
    border: 1px solid #ccc;
    cursor: pointer;
}
.empty{
    background-color: #f9f9f9;
}
.day:hover{
    background-color:  rgb(25, 111, 186);
    color: #fff;
}